
<template>
<div>
    <div v-for="(post, index) in posts" :key="index" style="margin: 20px;display: flex;justify-content: center;flex-wrap: wrap; width: 100%;">

        <div style="max-width: 300px;min-width:250px;margin-left: 5%;margin-bottom: 5%;vertical-align: top;background:rgb(235, 24, 189);">
        
            <img :src="post.imgURL" width="300px" height="auto" alt="imagen-no-disponible.png">

           <div >
             <h5 style="text-align: center;color: white;"> {{post.numero}} - {{post.fecha}}</h5>
             <p  style="color: white;">{{post.parrafo}}</p>
             <br><p style="color: yellow;"><a :href="post.enlace"> {{post.enlace}}</a></p>
             
           </div>

       </div>


    </div>
    </div>
</template>

<script>

export default {
  data() {
    return {
      posts: [],
      postq: {}
    };
  },
    created(){
    this.listarPosts();
  },
  methods:{
    listarPosts(){

      this.axios.get('images/all')
      .then((response) => {
        this.posts = response.data;
      })
      .catch((e)=>{
        console.log('error' + e);
      })
    }
    }
}

</script>